:root {
	--font-default: 'Gilroy';
	--fw-medium: 500;
	--fw-extra: 800;

	--blue-gradient: linear-gradient(180deg, #56D2E3 0%, #1991D7 100%);

	--c-title: #2A5E8D;
	--c-text: #5D5A5B;
}
* {
	outline: none;
	border: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

body {
	position: relative;
	font-family: var(--font-default);
	font-size: rem(18);
	// height: 100%;
	overflow-x: hidden;
	min-width: 320px;
	// -webkit-overflow-scrolling: touch;
	background-color: #F5F5F5;
}

.container {
	max-width: rem(1160);
	margin: 0 auto;
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%;
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}

.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}