.footer {
	padding-bottom: rem(105);

	.section-header {
		margin-bottom: rem(52);
	}

	&__content {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;

		img {
			margin-right: rem(92);
			border-radius: 50%;
			flex: 0 0 rem(250);
			width: rem(250);
			height: rem(250);
		}

		ul {
			li {
				margin-bottom: rem(34);

				font-size: rem(24);
				line-height: rem(55);
				font-weight: bold;
				color: var(--c-text);

				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
}