.results {
	margin-bottom: rem(52);
	.container {
		position: relative;
	}

	.section-header {
		margin-bottom: rem(38);
	}

	.slider-button {
		position: absolute;
		top: 60%;
		width: 100%;

		&-prev, &-next {
			position: absolute;
			top: 0;

			span {
				font-size: rem(42);
				font-weight: var(--fw-extra);
				color: var(--c-text);
			}
		}

		&-prev {
			left: rem(-40);
		}

		&-next {
			right: rem(-40);
		}
	}

	&__item {
		background-color: white;
		padding: rem(32) rem(36);
		
		&-top {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			margin-bottom: rem(26);

			.image {
				flex: 0 0 rem(324);
				max-width: rem(324);
				margin-right: rem(46);

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			.list {
				margin-right: rem(36);
				margin-top: rem(25);
				display: flex;
				flex-direction: column;

				flex: 0 0 rem(420);
				max-width: rem(420);

				img {
					margin-bottom: rem(30);
					width: rem(108);
					height: rem(45);
				}

				ul {
					li {
						margin-bottom: rem(30);
						color: var(--c-text);

						&:last-child {
							margin-bottom: 0;
						}
					}
				}
			}

			.info {
				display: flex;
				// flex-wrap: wrap;
				align-items: center;
				align-self: flex-start;

				background-color: #279DD9;
				padding: rem(24) rem(20);

				margin-top: rem(30);
				
				flex: 0 0 rem(250);
				max-width: rem(250);

				img {
					margin-right: rem(16);
				}

				p {
					font-size: rem(24);
					font-weight: var(--fw-extra);
					color: white;
					text-transform: uppercase;
				}
			}
		}

		.text {
			line-height: rem(25);
		}
	}

	.swiper-pagination {
		position: relative;
		bottom: 0;
		margin-top: rem(28);

		z-index: 10;

		&-bullet {
			width: rem(20);
			height: rem(20);
			margin: 0 rem(20);
			opacity: 1;
			background-color: #C4C4C4;
		}

		&-bullet-active {
			background-color: #2A5E8D;
		}
	}
}