.preview {
	background: url(../img/section_2_bg.jpg) no-repeat top center;
	background-size: cover;
	padding: rem(52) 0;

	margin-bottom: rem(65);

	.container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}

	&__text {
		flex: 0 0 rem(735);
		max-width: rem(735);
		h2,h3 {
			font-size: rem(42);
			line-height: rem(55);
			text-transform: uppercase;
			color: white;
			font-weight: bold;
			margin-bottom: rem(20);
		}

		p {
			font-weight: bold;
			font-size: rem(24);
			line-height: rem(30);
			color: white;
		}
	}

	&__button {
		margin-left: rem(52);

	}
}