.program {
	margin-bottom: rem(115);
	
	.section-header {
		margin-bottom: rem(40);
	}

	.button {
		margin-top: rem(52);
		margin-left: auto;
		margin-right: auto;
	}

	&__items {
		max-width: rem(877);
		margin-left: auto;
		margin-right: auto;
	}

	&__item {
		margin-bottom: rem(15);
		padding: rem(12) rem(40);
		background-color: white;

		display: flex;
		flex-wrap: wrap;
		align-items: center;

		&:last-child {
			margin-bottom: 0;
		}

		.image {
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: 50%;
			background: var(--blue-gradient);
			width: rem(60);
			height: rem(60);
			margin-right: rem(50);
		}

		.text {
			flex: 1;

			p, b {
				color: var(--c-text);
			}

			b {
				font-size: rem(24);
				line-height: rem(26);
				font-weight: var(--fw-extra);
				
				display: block;
				
				span {
					background: var(--blue-gradient);
					background-clip: text;
					-webkit-text-fill-color: transparent;
				}
			}

			p {
				margin-top: rem(14);
			}
		}
	}

	&__item.gray {
		background-color: #ECECEC;

		.image {
			background: none;
			background-color: #F5F5F5;
		}
	}
}