.section-header {
	h1,h2,h3 {
		color: var(--c-title);
		text-transform: uppercase;
		font-size: rem(42);
		line-height: rem(55);
		font-weight: var(--fw-extra);
		text-align: center;
		margin-bottom: rem(12);
	}

	p {
		text-align: center;
		font-weight: var(--fw-extra);
		color: var(--c-text);
		font-size: rem(24);
		line-height: rem(30);
	}
}

.what {

	margin-bottom: rem(34);

	.section-header {
		margin-bottom: rem(45);
	}


	&__items {
		display: flex;
		flex-wrap: wrap;


	}

	&__item {
		position: relative;
		overflow: hidden;
		width: calc((100% - 40px) / 2);
		margin-right: rem(20);
		margin-bottom: rem(20);

		padding: rem(36) rem(20) rem(27) rem(30);
		background-color: white;

		display: flex;
		flex-wrap: wrap;

		&:nth-child(2n) {
			margin-right: 0;
		}


		.left {
			display: flex;
			flex-direction: column;

			.title {
				font-size: rem(24);
				line-height: rem(55);
				font-weight: var(--fw-extra);
				color: var(--c-text);
				text-transform: uppercase;
			}

			p {
				line-height: rem(25);
				color: var(--c-text);
			}
		}

		.right {
			position: absolute;
			bottom: rem(-5);
			right: rem(-20);
		}
	}

	.item_1 {
		.left {
			flex: 0 0 rem(396);
			max-width: rem(396);
		}

		.right {
			right: rem(-20);
			bottom: rem(-5);
		}
	}

	.item_2 {
		.left {
			flex: 0 0 rem(320);
			max-width: rem(320);
		}
	}

	.item_3 {
		.left {
			flex: 0 0 rem(360);
			max-width: rem(360);
		}

		.right {
			right: rem(5);
		}
	}

	.item_4 {
		.left {
			flex: 0 0 rem(202);
			max-width: rem(202);
		}

		.right {
			right: rem(42);
		}
	}

	.item_5 {
		.left {
			flex: 0 0 rem(396);
			max-width: rem(396);
		}

		.right {
			right: 0;
		}
	}

	.item_6 {
		.left {
			flex: 0 0 rem(347);
			max-width: rem(347);
		}

		.right {
			bottom: 27%;
			right: rem(20);
		}
	}

	.item_7 {
		.left {
			flex: 0 0 rem(360);
			max-width: rem(360);
		}

		.right {
			right: 0;
		}
	}

	.item_8 {
		.left {
			flex: 0 0 rem(347);
			max-width: rem(347);
		}

		.right {
			right: rem(20);
		}
	}
}