.contact_form {
	position: relative;
	top: rem(-64);
	margin-bottom: rem(34);

	> .container {
		
	}

	&__wrap {
		width: 100%;
		padding: rem(27) rem(28);
		background-color: white;
		border-radius: rem(10);

		display: flex;
		flex-wrap: wrap;
		align-items: flex-end
	}

	.field-set {
		margin-right: rem(28);

		&:first-child {
			flex: 0 0 rem(220);
			max-width: rem(220);
		}

		&:nth-child(2) {
			flex: 0 0 rem(279);
			max-width: rem(279);
		}

		&:nth-child(3) {
			flex: 0 0 rem(184);
			max-width: rem(184);
		}

		&:last-child {
			margin-right: 0;
		}
	}


}

.field-set {
	display: flex;
	flex-direction: column;

	.label {
		font-size: rem(18);
		font-weight: var(--fw-extra);
		color: #747474;
		margin-bottom: rem(11);
	}

	
}

.field {
	box-shadow: inset 0px 1px 7px rgba(0, 0, 0, 0.2);
	border-radius: rem(9);
	background-color: white;
	height: rem(56);
	padding: 0 rem(20);
	font-size: rem(18);
	font-weight: var(--fw-medium);
	color: #747474;

	transition: all 300ms ease;

	&:focus {
		box-shadow: inset 0px 1px 7px rgba(70, 168, 217, .8);
		// box-shadow: 1px 4px 8px rgb(70, 168, 217);
	}
}

.button {
	height: rem(67);
	border-radius: rem(52);
	background: var(--blue-gradient);
	display: flex;
	align-items: center;
	justify-content: center;

	padding: 0 rem(24);

	box-shadow: 1px 4px 8px #46A8D9;
	
	span {
		color: white;
		font-weight: bold;
		font-size: rem(20);
	}
}