.conference {
	overflow: hidden;
	margin-bottom: rem(65);

	.section-header {
		margin-bottom: rem(37);
	}

	&__items {
		display: flex;
		flex-wrap: wrap;


	}

	&__item {
		background-color: white;
		padding: rem(30) rem(38);
		flex: 0 0 rem(560);
		max-width: rem(560);

		&:first-child {
			margin-right: rem(20);

			height: rem(558);
		}

		.top {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			&__info {
				display: flex;
				flex-direction: column;

				flex: 0 0 rem(297);
				max-width: rem(297);
				

				.title {
					font-size: rem(24);
					margin-bottom: rem(5);
					font-weight: var(--fw-extra);
					line-height: rem(30);
					background: var(--blue-gradient);
					background-clip: text;
					-webkit-text-fill-color: transparent;
				}

				p {
					font-weight: var(--fw-extra);
					color: var(--c-text);
				}
			}
		}

		.bottom {
			margin-top: rem(15);
			max-width: rem(343);

			p {
				line-height: rem(25);
				color: var(--c-text);
				margin-bottom: rem(20);

				&:last-of-type {
					margin-bottom: 0;
				}
			}
		}
	}

	.flex.flex-col {
		flex: 0 0 rem(560);
		max-width: rem(560);
		height: rem(558);
		.conference__item {
			margin-bottom: rem(20);
			max-height: rem(270);
			margin-right: 0;

			&:last-child {
				margin-bottom: 0;
			}
		}

		
	}
}