.header {
	height: rem(711);
	background: url(../img/header_bg.png) no-repeat bottom center;
	background-size: cover;

	&__top {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		padding-top: rem(35);
		padding-bottom: rem(100);

		&-center {
			max-width: rem(385);

			p {
				text-align: center;
				font-size: rem(14);
				line-height: rem(20);
				color: white;
			}
		}

		&-right {
			.top {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				margin-bottom: rem(8);

				img {
					margin-right: rem(15);
				}


				b {
					font-weight: var(--fw-extra);
					font-size: rem(24);
					line-height: rem(30);
					color: white;
				}
			}

			.bottom {
				display: flex;
				justify-content: center;
				a {
					color: white;
				}
			}
		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;

		.left {
			flex: 0 0 rem(735);
			max-width: rem(735);

			h1 {
				font-weight: bold;
				font-size: rem(42);
				line-height: rem(55);
				color: white;
				margin-bottom: rem(20);
			}

			p {
				font-weight: var(--fw-medium);
				font-size: rem(24);
				line-height: rem(30);
				color: white;
			}
		}

		.right {
			position: relative;
			max-width: rem(400);

			img {
				position: relative;
				left: rem(-40);
				top: rem(-40);
			}

			.text-block {
				position: absolute;
				bottom: rem(120);
				right: 0;
				width: rem(272);
				background-color: white;
				border-radius: rem(10);
				padding: rem(15) 0;
				box-shadow: 5px 5px 33px rgba(0, 0, 0, 0.15);
				display: flex;
				flex-direction: column;
				align-items: center;

				b {
					margin-bottom: rem(5);
					background: var(--blue-gradient);
					background-clip: text;
					-webkit-text-fill-color: transparent;
					font-size: rem(24);
				}

				p {
					color: #393939;
					font-size: rem(18);
					font-weight: var(--fw-medium);
				}
			}
		}
	}
}