.excursion {
	margin-bottom: rem(118);

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}

	&__left {
		margin-right: rem(42);

		flex: 0 0 rem(294);
		max-width: rem(294);
	}

	&__right {
		flex: 0 0 rem(736);
		max-width: rem(736);

		padding-top: rem(50);

		.title {
			font-size: rem(42);
			font-weight: bold;
			line-height: rem(55);
			color: var(--c-text);
			margin-bottom: rem(37);
		}

		.button {
			align-self: flex-start;
		}
	}
}